<template>
    <ul>
        <li v-for="(item, i) in data" :key="i">
            <i class="u-icon">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 12 16" width="12" height="16" aria-hidden="true" fill="#555" v-if="item.public" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" d="M4 9H3V8h1v1zm0-3H3v1h1V6zm0-2H3v1h1V4zm0-2H3v1h1V2zm8-1v12c0 .55-.45 1-1 1H6v2l-1.5-1.5L3 16v-2H1c-.55 0-1-.45-1-1V1c0-.55.45-1 1-1h10c.55 0 1 .45 1 1zm-1 10H1v2h2v-1h3v1h5v-2zm0-10H2v9h9V1z"/></svg>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 12 16" width="12" height="16" fill="#dbab09" v-else="" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" d="M4 13H3v-1h1v1zm8-6v7c0 .55-.45 1-1 1H1c-.55 0-1-.45-1-1V7c0-.55.45-1 1-1h1V4c0-2.2 1.8-4 4-4s4 1.8 4 4v2h1c.55 0 1 .45 1 1zM3.8 6h4.41V4c0-1.22-.98-2.2-2.2-2.2-1.22 0-2.2.98-2.2 2.2v2H3.8zM11 7H2v7h9V7zM4 8H3v1h1V8zm0 2H3v1h1v-1z"/></svg>
            </i>
            <a class="u-title" target="_blank" :href="item.id | getCollectionLink">{{
                item.title || "无标题"
            }}</a>
            <div class="u-desc">
                <time class="u-desc-subitem">
                    <i class="el-icon-finished"></i>
                    发布 :
                    {{ item.created | dateFormat }}
                </time>
                <time class="u-desc-subitem">
                    <i class="el-icon-refresh"></i>
                    更新 :
                    {{ item.updated | dateFormat }}
                </time>
            </div>
            <el-button-group class="u-action">
                <el-button
                    size="mini"
                    icon="el-icon-edit"
                    title="编辑"
                    @click="post_edit(item.id)"
                ></el-button>
                <el-button
                    size="mini"
                    icon="el-icon-delete"
                    title="删除"
                    @click="post_del(item.id)"
                ></el-button>
            </el-button-group>
            <!-- </div> -->
        </li>
    </ul>
</template>

<script>
import { remove_collection } from "@/service/collection";
import { getTypeLabel,getLink } from "@jx3box/jx3box-common/js/utils";
import dateFormat from "@/utils/dateFormat";
export default {
    name: "",
    props: ["data"],
    data: function() {
        return {};
    },
    computed: {},
    methods: {
        post_edit(id) {
            location.href = "./#/collection/" + id;
        },
        post_del(id) {
            this.$alert("确定要删除这篇小册吗？", "确认信息", {
                confirmButtonText: "确定",
                callback: (action) => {
                    remove_collection(id).then((res) => {
                        res = res.data;
                        if (res.code === 200) {
                            this.$message({
                                type: "success",
                                message: `删除成功`,
                            });
                            this.page_change(this.page);
                        } else
                            this.$message({
                                type: "warning",
                                message: res.message,
                            });
                    });
                },
            });
        },
    },
    filters: {
        dateFormat: function(val) {
            return dateFormat(new Date(val*1000));
        },
        getCollectionLink: function(val) {
            return getLink('collection',val);
        },
    },
    mounted: function() {},
    components: {},
};
</script>

<style scoped lang="less">
@import "../assets/css/work.less";
.u-collection-id{
    .mr(10px);
}
</style>
